.intro{
    display:flex;
    height:77vh;
    margin-top:6rem;
}
.i-left{
    display:flex;
    position: relative;
    flex:1;
    flex-direction: column;
    gap: 2rem;
}
.i-name{
    display:flex;
    flex-direction: column;
}
.i-name>:nth-child(1){
    color: var(--black);
    font-weight: bold;
    font-size: 2.5rem;
}
.i-name>:nth-child(2){
    color: var(--orange);
    font-weight: bold;
    font-size: 2.5rem;
}
.i-name>:nth-child(3){
    color: var(--gray);
    font-weight: 100;
    font-size: 17px;
    margin-top: 10px;
}
.i-button{
    width: 6rem;
    height:2rem;
}
.i-icons{
    margin-top: 3rem;
    display:flex;
    gap:0rem;
}
.i-icons>*{
    transform:scale(0.5) /*reducing size */
}
.i-right{
    flex: 1;
    position: relative;
}
.i-right>img{
    position: absolute;
    /* z-index:1; */
}
.i-right>:nth-child(1){
    transform: scale(0.69);
    left:-15%;
    top: -9%;
}
.i-right>:nth-child(2){
    top: -4.6rem;
    transform: scale(0.67);
    left:-3rem;
}
.i-right>:nth-child(3){
    transform: scale(1.4);
    height: 55vh;
    left:28%;
}
.i-right>:nth-child(4){
    transform: scale(0.6);
    top:-19%;
    left:-24%;
    border-radius: 50%;
    padding:0;
}
.floatDiv{
    position: relative;
    width:40rem;
}
.blur{
    position:absolute;
    width: 22rem;
    height: 14rem;
    border-radius: 50%;
    background: #edd0ff;
    filter:blur(72px);
    z-index: -9;
    top:-18%;
    left:56%;
}

@media screen and (max-width: 600px){
    .intro{
        flex-direction: column;
        height: 64rem;
        gap:7rem;
    }
    .i-right{
        margin-top: -20rem;
        transform: scale(0.8);
        left: -5rem;
    }
    .i-right .blur{
        display: none;
    }
    .floatingdiv:nth-of-type(1){
        top: -7rem !important;
    }

}