.floatingdiv{
    justify-content: space-around;
    display: flex;
    background: white;
    box-shadow: var(--boxShadow);
    border-radius: 17px;
    align-items: center;
    padding:0px 28px 0px 0px;
    height:4.5rem;
    z-index: 1;
    width: 35%;
}
.floatingdiv>img{
    transform: scale(0.4);
}
.floatingdiv>span{
    color: black;
}